@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0,0,0,0);

}
html,
body {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}


.attributions{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.credits{
  text-align: center;
}

.attributions > *:not(.credits){
  margin: .8em 0;
}

.pictures-frame,
.arrows-container {
  width: 600px;
  height: 480px;
}

.image {
  background-repeat: no-repeat;
  background-image: url(../images/ju-guan-D-jLxBtEwaA-unsplash.jpg);
  background-size: cover;
  border-radius: 25px;
  background-position: center center;
  transition: all 300ms ease-in-out 100ms;
}

.arrows-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 10px solid black;
  border-radius: 25px;
  box-shadow: 4px 3px 3px rgba(0, 0, 0, 0.329);
}

.circles-container {
  position: relative;
  width: 600px;
  height: 40px;
  bottom: 50px;
  display: flex;
  justify-content: center;
  gap: 1em;
  align-items: center;
}

.circle {
  height: 30px;
  width: 30px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  cursor: pointer;
}
.circle:focus{
  background-color: #ffffff;
  outline: none;
}

.bi-caret-left,
.bi-caret-right {
  font-size: 4rem;
  color: white;
  cursor: pointer;
}

@media screen and (max-width: 700px) {
  .pictures-frame,
  .arrows-container {
    width: 300px;
    height: 240px;
  }
  .bi-caret-left,
  .bi-caret-right {
    font-size: 2rem;
    color: white;
  }

  .circles-container {
    position: relative;
    width: 300px;
    height: 30px;
    bottom: 40px;
  }

  .circle-1,
  .circle-2,
  .circle-3,
  .circle-4,
  .circle-5 {
    height: 15px;
    width: 15px;
    border: 1px solid #ffffff;
    border-radius: 50%;
  }
}
